.wrapper {
    height: 100vh;   
    overflow: hidden;
    display: grid;
    grid-template-columns: minmax(50px, 280px) 1fr;
    background-image: linear-gradient(to bottom, 
                        #0F4454 15%, 
                        #194C5D 25%, 
                        #4C7885 45%, 
                        #A1C0C5 60%, 
                        #f5f5f5 100%);
}

.task-scroll {
    padding: 15px;
    overflow-y: scroll;
    height: 100vh;
}

/* Left */
.sidebar {
    display: flex;
    flex-direction: column;
    background-color: #F7F7F7;
}

.search {
    padding: 10px 15px 10px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;
    background-color: #0F4454;
    height: 45px;
    overflow: hidden;
}

.search button {
    background-color: transparent;
    border: none;
    color: white;
    padding: 0;
}

.search input {
    border: none;
    background-color: transparent;
    color: white;
}

.userbar {
    height: 50px;
    padding: 0 10px 0 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.userbar button {
    background-color: transparent;
    border: none;
}

.userbar button:first-child {
    float: left;
}

.userbar span {
    margin: 0 10px;
    font-size: 15px;
    line-height: 32px;
    font-weight: 500;
    color: #262626;
}

.userbar i {
    color: #818181;
}

.userbar i:hover {
    color: #356573;
}

.userbar img {
    width: 32px;
    height: 32px;
    border-radius: 100px;
}

.taskslist {
    padding: 0;
}

.taskslist ul {
    padding: 0;
    list-style-type: none;
    font-size: 16px;
    color: black;
}

.taskslist li {
    padding: 10px 10px  ;
}

.taskslist li button {
    background-color: transparent;
    border: none;
}

.taskslist .list--active {
    background-color: #E0EEFA;
}

.taskslist i {
    margin-right: 10px;
    font-size: 20px;
    color:  lightgrey;
}

.createbutton {
    border-top: 1px solid rgba(0,0,0,0.1);;
}

.createbutton button {
    position: relative;
    border: none;
    background-color: transparent; 
    text-align: left;
    padding: 9px 50px;
    color: #328ad6;
    font-weight: 400;
}

.createbutton button i {
    position: absolute;
    left: 20px;
    top: 12px;
    font-size: 18px;
}

/* List header */
.list-toolbar {
    background-color: #2F6676;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 45px;
    overflow: hidden;
    padding: 0 20px;
}

.list-toolbar .title {
    font-size: 20px;
    color: #fff;
    font-weight: 300;
    white-space: nowrap;
    overflow: hidden;
    margin: 0;
}

.actionBar {
    display: flex;
    flex-direction: row;
}

.actionBar i {
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 14px;
    margin: 0 5px;
}

.actionBar i span {
    margin-top: 4px;
    font-weight: 400;
    font-size: 10px;
    font-family: 'Lato';
}

/* Botón */
.boton--light { 
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    display: inline-block;
}

.btn.boton--light {
    color: white;
}

.boton--md {
    padding: 4px 10px;
    border-radius: 5px;
}


/* Add task */
.addTask {
    position: relative;
    color: #A7BDC4 !important;
}

.addTask .input--button {
    padding-left: 45px;
}

.addTask .input--button::placeholder {
    color: #A7BDC4;
}

.addTask .addTask-btn {
    position: absolute;
    top: 2px;
    left: 6px;
    margin: 0;
    font-size: 16px;
    color: #A7BDC4;
}

.addTask input {
    background-color: #285E6E;
    border: none;
    padding: 10px;
    color: #f7f7f7;
    width: 100%;
    border-radius: 5px;
}

.addTask-actions {
    position: absolute;
    top: 2px;
    display: block;
}

.addTask-actions--right {
    right: 5px;
}

/* Tasks list */
.heading {
    padding: 3px 10px;
    text-transform: uppercase;
    display: inline-block;
    border-radius: 3px;
    text-rendering: optimizeLegibility;
    letter-spacing: 1px;
    font-weight: 400;
    color: white !important;
}

.heading--normal {
    background-color: #285e6e;
    line-height: 16px;
    font-size: 11px;
}

.taskList {
    padding: 0;
    margin: 15px 0;
    list-style: none;
}

.taskList li {
    width: 100%;
    margin-bottom: 1px;
}

.taskItem-body {
    height: 46px;
    padding: 7.5px 15px;
    background-color: white;
    border-radius: 3px;
    display: flex;
    align-items: center; 
}

.taskItem-body i {
    color: rgba(0,0,0,0.35);
}

.taskItem-body .task-check {
    background-color: transparent;
    border: none;
}

.taskItem-body .task-name {
    width: 100%;
    margin-left: 10px;
}

.tasks--done i {
    color: #788C92;
}

.taskItem-body--active {
    background-color: #E1F2FE;
}

.tasks--done .taskItem-body {
    background-color: #BDCDD2;
}

.taskname-wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 10px;
}

.taskname-wrapper span {
    font-size: .9rem;
    text-decoration: line-through;
    margin: 0;
    color: #1E333A;
}

.taskname-wrapper small {
    font-size: .65rem;
    line-height: .65rem;
    color: #1E333A;
}